import React from 'react';
import { createHashRouter, RouterProvider } from 'react-router-dom';
import { useEffect, useState } from 'react';
import './App.css';
import jwtDecode from 'jwt-decode'
import ExerciseDetail from './pages/ExerciseDetail';
import Home from './pages/Home';
import ProtectedRoute from './components/ProtectedRoute';
import NotFound from './components/NotFound'
import Layout from './components/Layout'
import Register from './components/Register'
import Login from './components/Login'
import Excn from './components/Excn';

function App() {

  useEffect(() => {

    if (localStorage.getItem('userToken') !== null) {
      savUserData()
    }

  }, []);
  const [userdata, setUserdata] = useState(null);
  function savUserData() {
    let encodedToken = localStorage.getItem('userToken');
    let decodedToken = jwtDecode(encodedToken);
    setUserdata(decodedToken);

  }

  let routers = createHashRouter([
    {
      path: '', element: <Layout setUserdata={setUserdata} userdata={userdata} />, children: [
        { index: true, element: <Home /> },
        { path: "Exercises", element: <ProtectedRoute ><Excn/></ProtectedRoute> },
        { path: "/exercise/:id", element: <ProtectedRoute ><ExerciseDetail/></ProtectedRoute> },
        { path: 'Register', element: <Register /> },
        { path: 'Login', element: <Login savUserData={savUserData} /> },
        { path: "*", element: <NotFound /> },
      ]
    }
  ])

  return <>


    <RouterProvider router={routers}></RouterProvider>

  </>

}

export default App;
