import React from 'react';
import { Link } from 'react-router-dom';
import logo from '../assets/icons/logo.png'


export default function Navbar({ userdata, logout }) {


  return <>
    <nav className="navbar position-fixed fixed-top navbar-expand-sm navbar-light bg-light">
      <div className="container">
        <Link className="navbar-brand px-3" to="/"><img className='w-25' src={logo} alt="" /> BurnBright</Link>
        <button className="navbar-toggler d-lg-none mx-5 ms-5" type="button" data-bs-toggle="collapse" data-bs-target="#collapsibleNavId" aria-controls="collapsibleNavId"
          aria-expanded="false" aria-label="Toggle navigation">
          <i class="fa-solid fa-bars  py-1 px-1"></i>
        </button>
        <div className="collapse navbar-collapse" id="collapsibleNavId">
          {userdata !== null ? <ul className="navbar-nav mt-2 mt-lg-0">
            <li className="nav-item list-unstyled mx-2">
              <Link className="nav-link" to="/"> <i className="fa-solid fa-house"></i> Home</Link>
            </li>

            <li className="nav-item  list-unstyled mx-2 ">
              <Link className="nav-link" to="Exercises"> <i className="fa-solid fa-person-running"></i> Exercises</Link>
            </li>

          </ul> : null}





          <ul className="navbar-nav ms-auto mt-2 mt-lg-0 px-5 me-5">

            {userdata == null ? <>
              <li className="nav-item">
                <Link className="nav-link cursor-pointer" to="Login">Login <i className="fa-solid fa-right-to-bracket mx-2"></i></Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link cursor-pointer" to="Register">Register <i className="fa-solid fa-user-plus mx-2"></i></Link>
              </li>
            </> : <>


              <li className="nav-item cursor-pointer">
                <Link onClick={logout} className="cursor-pointer nav-link" >Logout <i className="fa fa-sign-out mx-1" aria-hidden="true"></i></Link>
              </li>
            </>}





          </ul>

        </div>
      </div>
    </nav>


  </>
}


