import React, { useEffect, useState } from 'react';

import { exerciseOptions, fetchData } from '../utils/fetchData';
import HorizontalScrollbar from './HorizontalScrollbar';

const SearchExercises = ({ setExercises, bodyPart, setBodyPart }) => {
  const [search, setSearch] = useState('');
  const [bodyParts, setBodyParts] = useState([]);

  useEffect(() => {
    const fetchExercisesData = async () => {
      const bodyPartsData = await fetchData('https://exercisedb.p.rapidapi.com/exercises/bodyPartList', exerciseOptions);

      setBodyParts(['all', ...bodyPartsData]);
    };

    fetchExercisesData();
  }, []);

  const handleSearch = async () => {
    if (search) {
      const exercisesData = await fetchData('https://exercisedb.p.rapidapi.com/exercises', exerciseOptions);

      const searchedExercises = exercisesData.filter(
        (item) => item.name.toLowerCase().includes(search)
          || item.target.toLowerCase().includes(search)
          || item.equipment.toLowerCase().includes(search)
          || item.bodyPart.toLowerCase().includes(search),
      );

      window.scrollTo({ top: 1800, left: 100, behavior: 'smooth' });

      setSearch('');
      setExercises(searchedExercises);
    }
  };

  return <>
    <div className="container">
      <div className="row">
        <div className="col-md-12 text-center pb-4">
          <h3 className='fw-bolder'>Discover Your Workout !</h3>
        </div>
        <div className="col-md-12">
          <div className="input-group mb-3">
            <input type="text" value={search} onChange={(e) => setSearch(e.target.value.toLowerCase())} className="form-control border-black py-2" placeholder="Search Exercises" aria-label="Search Exercises" aria-describedby="button-addon2"></input>
            <button onClick={handleSearch} className="btn border-black " type="button" id="button-addon2"><i className="fa-brands fa-searchengin fa-fade fa-xl"></i></button>
          </div>
        </div>
        <div className="col-md-12 py-3 ">
        <HorizontalScrollbar data={bodyParts} bodyParts setBodyPart={setBodyPart} bodyPart={bodyPart} />
        </div>
      </div>
    </div>


  </>
};

export default SearchExercises;
